@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap);
:root {
  --clr-black: #22161c;
  --clr-white: #fff;
  --clr-silver: #f2f1ef;
  --clr-purple-1: #2a1c35;
  --clr-purple-2: #6e304b;
  --clr-purple-3: #8f7aa1;
  --clr-purple-4: #a37c82;
  --clr-purple-5: #af969c;
  --clr-purple-6: #57394a;
  --clr-yellow-1: #814136;
  --clr-yellow-2: #e2ae6c;
  --clr-yellow-3: #bf0603;
  --transition: all 0.5s linear;
  --margin-small: 0.5rem;
  --margin-large: 1rem;
  --padding-small: 0.5rem;
  --padding-large: 1rem;
  --radius-small: 0.5rem;
  --radius-large: 1rem;
  --dark-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.3), 0 6px 10px 0 rgba(0, 0, 0, 0.3);
  --light-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1),
    0 6px 20px 0 rgba(0, 0, 0, 0.1);
  --max-width: 1170px;
  --fixed-width: 620px;
  --border: 0.1px solid var(--clr-black);
}
::-webkit-scrollbar {
  display: none;
}

/*Mobile */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box !important;
}
body {
  display: flex;
  flex-direction: column;
  font-family: "Poppins";
  background-image: url(/static/media/body_background.177e57fe.png);
  background-repeat: repeat;
  background-size: 80%;
  background-position-y: -2rem;
  background-position-x: -62rem;
}

.app {
  display: flex;
  flex-direction: column;
}

nav {
  overflow: hidden;
  z-index: 5;
  position: fixed;
  top: 0rem;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  font-size: 1.1rem;
  padding: 0 0.5rem;
  padding: 0 var(--padding-small);
  color: #f2f1ef;
  color: var(--clr-silver);
  background-color: #2a1c35;
  background-color: var(--clr-purple-1);
  border-bottom: 2px solid #22161c;
  border-bottom: 2px solid var(--clr-black);
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1),
    0 6px 20px 0 rgba(0, 0, 0, 0.1);
  box-shadow: var(--light-shadow);
}

nav ul {
  display: flex;
  flex-direction: row;
  list-style: none;
  padding: 0.5rem;
  padding: var(--padding-small);
}
nav ul li {
  margin: 0 0.5rem;
  margin: 0 var(--margin-small);
}

.total-amount {
  float: right;
  text-align: center;
  font-size: 1rem;
  width: 1.5rem;
  height: 1.5rem;
  margin-left: -0.2rem;
  margin-top: -0.4rem;
  color: #f2f1ef;
  color: var(--clr-silver);
  text-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1),
    0 6px 20px 0 rgba(0, 0, 0, 0.1);
  text-shadow: var(--light-shadow);
  background-color: #bf0603;
  background-color: var(--clr-yellow-3);
  border-radius: 5rem;
}
nav h1,
nav li:hover {
  --clr-silver: var(--clr-yellow-2);
}

.container {
  min-height: calc(100vh - 1rem);
  display: flex;
  flex-direction: column;
  width: 100%;
  transition: all 0.5s linear;
  transition: var(--transition);
}
.checkout {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

@media screen and (min-width: 800px) {
  nav {
    font-size: 1.8rem;
    padding: 0 1rem;
    padding: 0 var(--padding-large);
  }
  nav ul li {
    margin: 0 1rem;
    margin: 0 var(--margin-large);
  }
}

.assistant {
  z-index: 3;
  position: sticky;
  top: 90%;
  right: 1%;
  display: flex;
  flex-direction: row;
  align-self: flex-end;
  justify-content: space-between;
  align-items: center;
}
.assistant img {
  width: 4rem;
  height: 4rem;
  border: 0.2rem solid var(--clr-yellow-3);
  border-radius: 5rem;
}
.assistant img:hover {
  border-color: var(--clr-yellow-2);
}
.assistant-content {
  overflow: hidden;
  max-width: 15rem;
  max-height: 5rem;
  font-size: 1.1rem;
  color: white;
  background-color: var(--clr-yellow-3);
  padding: 0.2rem;
  border-radius: var(--radius-small);
}

@media screen and (min-width: 800px) {
  .assistant {
    z-index: 3;
    position: sticky;
    top: 85%;
  }
  .assistant img {
    width: 6rem;
    height: 6rem;
  }
  .assistant-content {
    padding: var(--padding-small) var(--padding-large);
  }
}

.intro {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  margin-bottom: var(--margin-large);
}
/* .intro-info  */
.intro-info {
  margin: 0 var(--margin-large);
  color: var(--clr-purple-1);
  font-size: 1.2rem;
}
.intro-info p {
  margin: var(--margin-large) 0;
  color: black;
  font-size: 1rem;
  text-align: justify;
}

/* .intro-slider */
.section-center {
  height: 23rem;
  position: relative;
  display: flex;
  overflow: hidden;
  text-align: center;
  margin: 0 auto;
}
.intro-slider .product-img {
  height: 15rem;
  box-shadow: var(--dark-shadow);
  border-top: 1px solid rgb(190, 188, 188);
  border-radius: var(--radius-large);
  margin-bottom: var(--margin-large);
  object-fit: cover;
}

.intro-slider article h1 {
  font-size: 1rem;
  text-transform: uppercase;
  color: var(--clr-purple-1);
}

.prev,
.next {
  position: absolute;
  top: 7rem;
  width: 4rem;
  height: 3rem;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  font-size: 3rem;
  color: var(--clr-purple-1);
  background-color: transparent;
  border-color: transparent;
  border-radius: var(--radius-large);
  box-shadow: var(--light-shadow);
  margin: var(--margin-large);
  transition: var(--transition);
  cursor: pointer;
}
.prev:hover,
.next:hover {
  background: var(--clr-yellow-2);
}
.prev {
  left: 0;
}
.next {
  right: 0;
}

.intro-slider article {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: var(--transition);
}
.intro-slider article.activeSlide {
  opacity: 1;
  -webkit-transform: translateX(0);
          transform: translateX(0);
}
.intro-slider article.lastSlide {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}
.intro-slider article.nextSlide {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}

/* featured-products section  */
.featured-products {
  box-shadow: var(--dark-shadow);
  background-image: linear-gradient(
    270deg,
    var(--clr-purple-3),
    var(--clr-silver),
    var(--clr-purple-5)
  );
}
.featured-products h1 {
  text-align: center;
  padding: var(--padding-large);
  color: var(--clr-purple-2);
}

.home-slider {
  display: flex;
  flex-direction: row;
  align-items: center;
  overflow-x: auto;
}

.home-slider .card {
  min-width: 70%;
  height: 30rem;
}
.home-slider .card img {
  min-height: 15rem;
}
/* blank white */
.between {
  height: 30rem;
  background-color: var(--clr-white);
  background-image: linear-gradient(
      45deg,
      rgba(0, 0, 0, 0.5),
      rgba(0, 0, 0, 0.5)
    ),
    url(/static/media/customer_care.440a519b.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top;
}

/* home services promo part */
.home-promo {
  font-size: 0.8rem;
  box-shadow: var(--dark-shadow);
  background-image: linear-gradient(
    270deg,
    var(--clr-purple-3),
    var(--clr-silver),
    var(--clr-purple-5)
  );
}

.home-services {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}
.home-services h1 {
  font-size: 3rem;
}
.home-services h2 {
  font-size: 2rem;
  color: var(--clr-purple-1);
}
.home-services span {
  font-size: 1.5rem;
  color: var(--clr-purple-2);
  font-weight: 700;
}

.blank {
  color: var(--clr-purple-2);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.services {
  display: flex;
  flex-direction: column;
  margin: var(--margin-large);
}
.service {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  background: var(--clr-white);
  margin: var(--margin-large);
  padding: var(--padding-large);
  border-radius: var(--radius-large);
  box-shadow: var(--dark-shadow);
}

.service i {
  font-size: 8rem;
}
.home-register {
  display: flex;
  flex-direction: column;
}
.search {
  display: none;
  flex-direction: column;
  align-items: center;
  font-size: 1.5rem;
  color: var(--clr-purple-2);
}
.search input {
  width: 70%;
  font-size: 1.2rem;
  border: 0;
  box-shadow: var(--light-shadow);
  padding: 1.3rem;
  margin: var(--margin-large);
  border-radius: var(--radius-large);
}

@media screen and (min-width: 800px) {
  .intro {
    flex-direction: row;
  }
  .intro-info {
    width: 30%;
    font-size: 2.2rem;
    line-height: 2.3;
  }
  .intro-info p {
    font-size: 1.4rem;
    line-height: 1.6;
  }

  .intro-slider {
    width: 50%;
    margin: var(--margin-large);
    margin-top: 0;
  }
  .section-center {
    height: 44rem;
  }
  .intro-slider .product-img {
    height: 35rem;
  }
  .intro-slider article h1 {
    font-size: 1.4rem;
  }
  .prev,
  .next {
    top: 18rem;
  }

  .featured-products {
    height: 45rem;
  }

  .featured-products h1 {
    font-size: 3rem;
  }
  .home-slider .card {
    min-width: 25%;
    height: 35rem;
  }
  .home-slider .card img {
    min-height: 20rem;
  }

  .home-services {
    height: 40rem;
    flex-direction: row;
    font-size: 1.3rem;
  }
  .home-services h1 {
    font-size: 6rem;
  }
  .home-services h2 {
    font-size: 3rem;
  }
  .home-services span {
    font-size: 2rem;
  }

  .services {
    flex-direction: row;
  }
  .service {
    height: 25rem;
  }
  .search {
    display: flex;

    margin-top: var(--margin-large);
  }
}

@media screen and (min-width: 800px) and (min-height: 840px) {
  .section-center {
    height: 55rem;
  }
  .intro-slider .product-img {
    height: 45rem;
  }
  .prev,
  .next {
    top: 23rem;
  }

  .featured-products {
    height: 55rem;
  }

  .featured-products h1 {
    font-size: 3rem;
  }
  .home-slider .card {
    height: 45rem;
  }
  .home-slider .card img {
    min-height: 28rem;
  }
  .home-slider .card i {
    font-size: 2.5rem;
  }
  .between {
    height: 35rem;
  }

  .home-promo {
    height: 58rem;
  }

  .home-services {
    height: 50rem;

    font-size: 1.3rem;
  }
  .home-services h1 {
    font-size: 8rem;
  }
  .home-services h2 {
    font-size: 4rem;
  }
  .home-services span {
    font-size: 2.5rem;
  }

  .services {
    flex-direction: row;
  }
  .service {
    height: 25rem;
  }
}

.products-container {
  background-color: #fff;
}
.products-list {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  margin-bottom: 4rem;
}

.card {
  overflow: hidden;
  width: 80%;
  height: 25rem;
  display: flex;
  flex-direction: row;
  background-color: var(--clr-white);
  font-size: 1.2rem;
  margin: var(--margin-large);
  transition: var(--transition);
  padding-bottom: var(--padding-small);
  box-shadow: var(--light-shadow);
  border-radius: 3rem;
}

.card-info {
  width: 90%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: var(--radius-large);
  color: var(--clr-purple-2);
  padding-left: 3rem;
}
.card-info h3 {
  font-size: 0.9rem;
  padding-bottom: 0.5rem;
  border-bottom: 1px solid var(--clr-purple-2);
}
.info-price {
  padding: var(--padding-small);
  color: white;
  background-color: var(--clr-yellow-3);
  border-radius: var(--radius-large);
  text-decoration: none;
}

.card-info img {
  object-fit: cover;
  height: 13rem;
  margin: 2rem;
}

.card-actions {
  cursor: pointer;
  font-size: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  -webkit-transform: translateX(3.5rem);
          transform: translateX(3.5rem);
  transition: var(--transition);
}
.card:hover {
  box-shadow: var(--dark-shadow);
  box-shadow: 0 4px 8px 0 var(--clr-purple-5), 0 6px 10px 0 var(--clr-purple-3);
}
.card:hover .card-actions {
  transition: var(--transition);
  -webkit-transform: translateX(-0.3rem);
          transform: translateX(-0.3rem);
}

@media screen and (min-width: 800px) {
  .products-list {
    flex-direction: row;
    flex-wrap: wrap;
    margin: 4rem 0;
  }
  .card {
    width: 25%;
  }
  .card-info h3 {
    font-size: 1.2rem;
  }
}

/* team-slider */
.team {
  width: 100%;
}

.team-center {
  height: 25rem;
  position: relative;
  display: flex;
  overflow: hidden;
  text-align: center;
  margin: 0 auto;
  background-color: rgba(121, 78, 170, 0.1);
  border-radius: 5rem;
}

.person-img {
  height: 10rem;
  width: 10rem;
  object-fit: cover;
  margin-bottom: var(--margin-small);
  border: 0.2rem solid var(--clr-black);
  border-radius: 50rem;
  box-shadow: var(--dark-shadow);
}

.team article h4 {
  text-transform: uppercase;
  margin-bottom: 0.25rem;
}

.team .text {
  max-width: 45rem;
  margin: 0 auto;
  margin-top: var(--margin-small);
  line-height: 1.3rem;
  color: var(--clr-black);
}

.team .icon {
  font-size: 2rem;
  margin-top: 1rem;
  color: var(--clr-black);
}
.team .title {
  color: var(--clr-yellow-3);
}
.team .prev,
.team .next {
  position: absolute;
  top: 11rem;
  width: 4rem;
  height: 3rem;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  font-size: 3rem;
  color: var(--clr-purple-1);
  background-color: transparent;
  box-shadow: var(--light-shadow);
  border-color: transparent;
  border-radius: var(--radius-large);
  transition: var(--transition);
  cursor: pointer;
}
.prev:hover,
.next:hover {
  background: var(--clr-yellow-2);
}
.prev {
  left: 0;
}
.next {
  right: 0;
}

.team article {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: var(--transition);
}
article.activeSlide {
  opacity: 1;
  -webkit-transform: translateX(0);
          transform: translateX(0);
}
article.lastSlide {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}
article.nextSlide {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}

.details {
  display: flex;
  flex-direction: column;
  padding: 0 var(--padding-large);
  color: var(--clr-purple-1);
  margin-bottom: 3rem;
}

.details-info {
  display: flex;
  flex-direction: column;
  font-size: 1.1rem;
  color: black;
  margin-bottom: 3rem;
}

.details-info h1 {
  padding-bottom: var(--padding-large);
  border-bottom: 1px solid var(--clr-black);
}

/* message and team */
.details-contact {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}
.message {
  display: flex;
  flex-direction: column;
  align-self: flex-start;
  font-size: 1.2rem;
}

form input {
  border: 1px solid var(--clr-black);
  padding: var(--padding-large);
  border-radius: var(--radius-large);
}
#messageText {
  padding: var(--padding-large);
  height: 10rem;
  border: 1px solid var(--clr-black);
  border-radius: var(--radius-large);
}
.form-control {
  display: grid;
  grid-template-columns: 8rem 1fr;
  align-items: center;
  margin: var(--margin-large) 0;
}

.btn {
  display: inline-block;
  background: var(--clr-purple-1);
  color: var(--clr-white);
  border-radius: var(--radius-large);
  padding: 1.2rem;
  margin: var(--margin-small);
  font-size: 1rem;
  text-decoration: none;
  border: none;
  box-shadow: var(--dark-shadow);
  transition: var(--transition);
  cursor: pointer;
}
.btn:hover {
  background: var(--clr-yellow-2);
  color: var(--clr-black);
  border-color: var(--clr-black);
}

@media screen and (min-width: 800px) {
  .details-info {
    width: 60%;
  }
  .message {
    width: 35%;
  }
  .btn {
    padding: 1.2rem 1.5rem;
    font-size: 1.4rem;
  }
  .details-contact {
    flex-direction: row;
  }
  .team {
    width: 50%;
  }
  .team-center {
    height: 30rem;
  }
  .person-img {
    height: 18rem;
    width: 18rem;
  }
  .team .prev,
  .team .next {
    top: 13rem;
  }
}

@media screen and (min-width: 800px) and (min-height: 840px) {
  .team-center {
    height: 40rem;
  }
  .person-img {
    height: 25rem;
    width: 25rem;
  }
  .team article h4 {
    font-size: 1.3rem;
  }
  .team .prev,
  .team .next {
    top: 18rem;
  }
}

.cart-profile-container {
  display: flex;
  flex-direction: column;
  padding: 0 var(--padding-large);
}

/* top row */
.register-signIn {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  font-size: 1.3rem;
}
.register,
.sign-in {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  margin-bottom: var(--margin-large);
}
.register-signIn .btn,
.register-signIn form {
  margin: 0;
}
.register-signIn a {
  margin: var(--margin-large);
  font-style: italic;
  font-size: 0.9rem;
  color: var(--clr-purple-1);
}

@media screen and (min-width: 800px) {
  .cart-profile-container {
    margin: var(--margin-large);
  }
  .register-signIn {
    flex-direction: row;
  }
  .register,
  .sign-in {
    width: 40%;
  }
  .register-signIn a {
    margin: var(--margin-large);
    font-style: italic;
  }
}

.profile {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  width: 100%;
  font-size: 0.8rem;
  color: var(--clr-purple-1);
  border-radius: var(--radius-large);
  background-color: rgba(121, 78, 170, 0.1);
}
.user-details,
.cart {
  display: flex;
  flex-direction: column;
}

.user-account,
.user-wishlist {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  padding: var(--padding-large);
  border: var(--border);
  border-radius: var(--radius-large);
  background-color: var(--clr-white);
}

.user-profile,
.user-account-btn {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
}
.profile-icon {
  width: 5rem;
  font-size: 5rem;
  margin: var(--margin-small);
  border: 0.2rem solid var(--clr-black);
  border-radius: 5rem;
  box-shadow: var(--light-shadow);
}

.user-messages {
  margin-top: var(--margin-small);
  height: 10rem;
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
  scroll-behavior: smooth;
  overscroll-behavior: contain;
}

.user-messages .message {
  width: 90%;
  font-family: cursive;
  background-color: rgba(135, 96, 138, 0.281);
  background-image: url(/static/media/profile_message.7362fcc8.png);
  background-repeat: no-repeat;
  background-size: 15%;
  background-position: right;
  background-position-y: 0.3rem;
  color: var(--clr-purple-2);
  margin: var(--margin-small);
  padding: var(--padding-large);
  border-radius: var(--radius-large);
  box-shadow: var(--light-shadow);
}
.icon {
  font-size: 1.5rem;
  align-self: flex-end;
  cursor: pointer;
}

/* Wish list */
.user-wishlist {
  padding: var(--padding-large);
  margin: var(--margin-large) 0;
  overflow-y: scroll;
  scroll-behavior: smooth;
  overscroll-behavior: contain;
}
.item {
  display: grid;
  align-items: center;
  grid-template-columns: auto 1fr auto;
  grid-column-gap: 1.5rem;
  margin: var(--margin-small) 0;
  font-size: 1.2rem;
}

.item img {
  width: 7rem;
  height: 8rem;
  border: var(--border);
  border-radius: var(--radius-small);
  object-fit: cover;
}
.item h5 {
  margin-bottom: var(--margin-small);
  font-weight: 500;
}
.item-price {
  color: white;
  width: 6rem;
  text-align: center;
  background-color: var(--clr-yellow-3);
  border-radius: var(--radius-small);
}

/* CART */

.cart-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 3rem;
  background-color: white;
  border-radius: 0 0 1rem 1rem;
  border: 1rem solid var(--clr-yellow-1);
  border-top: none;
}

.remove-btn {
  color: var(--clr-yellow-3);
  cursor: pointer;
  font-size: 0.9rem;
  background: transparent;
  border: none;
  margin-top: var(--margin-small);
  transition: var(--transition);
}
.remove-btn:hover {
  color: var(--clr-black);
}

.amount-btn {
  width: 1.2rem;
  background: transparent;
  cursor: pointer;
}

.amount {
  text-align: center;
  margin-bottom: 0;
  font-size: 1.25rem;
  line-height: 1;
}
hr {
  background: var(--clr-purple-4);
  border-color: transparent;
  border-width: 0.25px;
}

.cart article {
  opacity: 1;
}
.cart h2 {
  text-transform: uppercase;
  text-align: center;
}

.cart .cart-footer {
  margin-top: 2rem;
  text-align: center;
  margin-bottom: 3rem;
}
.cart-total h4 {
  text-transform: capitalize;
  display: flex;
  justify-content: space-between;
  margin-top: var(--margin-small);
}

@media screen and (min-width: 800px) {
  .profile {
    flex-direction: row;
    justify-content: space-around;
    font-size: 1.2rem;
  }
  .user-details,
  .cart {
    width: var(--fixed-width);
  }
  .user-account,
  .user-wishlist {
    align-content: space-between;
  }

  .user-profile {
    width: 70%;
    font-size: 1rem;
  }
}

@media screen and (min-width: 800px) and (min-height: 840px) {
  .profile {
    --margin-small: var(--margin-large);
  }
}

.product-info {
  display: flex;
  flex-direction: column;
  padding: var(--padding-small);
  transition: var(--transition);
  font-size: 1rem;
}
.additional-info {
  display: flex;
  flex-direction: column;
  align-content: space-around;
  line-height: 2;
  color: var(--clr-purple-1);
  padding: var(--padding-large) 0;
}
.additional-info p {
  line-height: 1.5;
  text-align: justify;
}
.product-info i {
  margin: var(--margin-large);
}
.additional-actions {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  font-size: 4rem;
  cursor: pointer;
}
.price {
  color: var(--clr-yellow-3);
  border-radius: var(--radius-large);
}

.additional-images {
  height: 70vh;
  width: 85%;
  display: flex;
  flex-direction: column;
  align-self: center;
  overflow-y: auto;
  scroll-behavior: smooth;
  overscroll-behavior: contain;
  transition: var(--transition);
  background-color: var(--clr-white);
  box-shadow: var(--dark-shadow);
  border-radius: var(--radius-large);
}

.additional-images img {
  object-fit: cover;
  width: 90%;
  height: 80%;
  margin: 2rem;
  border-radius: var(--radius-large);
}

@media screen and (min-width: 800px) {
  .product-info {
    flex-direction: row;
    justify-content: space-between;
    font-size: 1.4rem;
    padding: 0;
  }
  .additional-info {
    width: 30%;
    padding: 0 var(--padding-large);
  }
  .additional-images {
    flex-direction: row;
    justify-content: space-between;
    align-self: flex-start;
    overflow-x: auto;
    height: 85vh;
    width: 60%;
    padding-left: 5rem;
    border: 0.2rem solid var(--clr-purple-4);
    border-right: transparent;
    border-radius: 1rem 0 0 50rem;
  }
  .additional-images img {
    width: 70%;
    margin: 0 3rem;
  }
}

footer {
  z-index: 4;
  position: relative;
  bottom: 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  font-size: 1rem;
  color: var(--clr-silver);
  background: var(--clr-purple-1);
  padding-top: 2rem;
}

.table-map {
  display: flex;
  flex-direction: column;
}

/* table */
.footer-list {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.table-list {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  font-size: 0.7rem;
}

.row {
  display: flex;
  flex-direction: column;
}

footer ul {
  list-style: none;
  line-height: 2;
}
.place-info {
  text-align: center;
  margin: 1rem 0;
}

/* MAP */
.map-info {
  height: 15rem;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: var(--clr-purple-1);
}

.map {
  overflow: hidden;
  font-size: 2rem;
  border: 1px solid black;
  border-radius: var(--radius-large);
}

/* last part */
.copy-right {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  margin-top: var(--margin-large);
}
.links {
  display: flex;
  flex-direction: row;
}
.links a {
  margin: 0 var(--margin-small);
}
.links a:hover {
  --clr-silver: var(--clr-yellow-2);
}

@media screen and (min-width: 800px) {
  footer {
    font-size: 1.5rem;
  }
  .table-map {
    flex-direction: row;
    justify-content: space-evenly;
  }
  .footer-list {
    width: 60%;
  }
  .table-list {
    font-size: 1rem;
  }
}

